body {
  margin: 0;
  display: flex;
  flex-direction: column;
}
header {
  height: 100px;
  background-color: grey;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

input {
  padding: 10px 20px;
}
.nav {
  height: 5vh;
  border-bottom: 1px solid grey;
  width: 20vw;
  align-self: center;
  display: flex;
  justify-content: space-around;
}

.nav-btn {
  border: none;
  padding: 0px 20px;
  background-color: white;
  cursor: pointer;
}
.display {
  height: 90vh;
  overflow: auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.container {
  border: 2px black solid;
  width: 800px;
  height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  align-self: center;
  border-radius: 20px;
}

.address {
  font-size: 80px;
}

.icon {
  width: 100px;
  height: 100px;
}
.temp {
  font-size: 90px;
}

.tempAndIcon {
  display: flex;
}

.text {
  border-top: 2px solid rgba(128, 128, 128, 0.461);
  width: 90%;
  text-align: center;
}

.card {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-bottom: 2px black solid;
}

.daily-container {
  display: flex;
  flex-direction: column;
  width: 60vw;
  justify-content: flex-start;
}

.table {
  display: flex;
  align-items: center;
  height: 40px;
  width: 60vw;
  justify-content: space-around;
  border-bottom: 2px solid black;
}

.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  border: 8px solid;
  border-color: #000 #0000;
  animation: l1 1s infinite;
}
@keyframes l1 {
  to {
    transform: rotate(0.5turn);
  }
}
